import PouchDB from 'pouchdb';

export const db = new PouchDB('badges_achieved', {
  revs_limit: 1,
  auto_compaction: true,
});

export type Badge = {
  id: string; // "id" from badgesList
  _rev?: string; // for database update purposes
  icon: string;
  label: string;
  details: string;
  available: boolean;
  claimable?: boolean;
  achieved?: boolean;
  check: () => Promise<boolean>;
};
