import { Badge, db } from '.';

export const updateExistingBadge = async (
  badge: Badge | undefined,
  claimable: boolean,
  achieved?: Date,
) => {
  return await db.put(
    {
      _id: badge?.id,
      _rev: badge?._rev,
      claimable: claimable,
      achieved: achieved,
    },
    { force: true },
  );
};
