import React from 'react';
import styles from './BadgeDetails.module.scss';
import { badgesList } from '../../../../shared/db/badges/badgesList';
import { SingleBadge } from '../SingleBadge/SingleBadge';
import { Badge } from '../../../../shared/db/badges';

type Props = {
  selectedBadgeId: string;
};
export const BadgeDetails: React.FC<Props> = ({ selectedBadgeId }) => {
  const badge = badgesList.find(
    (badge: Badge) => badge.id === selectedBadgeId,
  ) as Badge;
  return (
    <div className={styles.root}>
      <SingleBadge
        icon={badge.icon}
        label={badge.label}
        discovered={badge.available}
        onClick={() => {}}
        details={badge.details}
        fullView
      />
    </div>
  );
};
