import { Badge } from '.';
import { db } from '.';
import { SingleDocResponse } from '..';
import { badgesList } from './badgesList';
import { updateExistingBadge } from './updateExistingBadge';

const getAllDbBadges = async () => {
  try {
    return await db.allDocs<Badge>({
      include_docs: true,
    });
  } catch (error: any) {
    if (error.name === 'not_found') {
      return undefined;
    }
    throw error;
  }
};

const unifyBadge = (
  badge: Badge,
  badgeDoc?: SingleDocResponse<Badge>,
): Badge => {
  console.log('badgeDoc', badgeDoc);
  return {
    ...badge,
    claimable: badgeDoc?.doc?.claimable,
    achieved: badgeDoc?.doc?.achieved,
    _rev: badgeDoc?.doc?._rev,
  };
};

const getUnifiedBadges = (
  badgesList: Badge[],
  badgesDocs?: PouchDB.Core.AllDocsResponse<Badge>,
): Badge[] => {
  console.log('badgeFromList', badgesList);

  if (!badgesDocs) {
    return badgesList;
  }

  return badgesList.map((badge) => {
    const badgeDoc: SingleDocResponse<Badge> | undefined =
      badgesDocs?.rows.find(
        (doc: SingleDocResponse<Badge>) => doc.id === badge.id,
      );
    return unifyBadge(badge, badgeDoc);
  });
};

const claimableCheck = async (badgesList: Badge[]): Promise<Badge[]> => {
  try {
    return await Promise.all(
      badgesList.map(async (badge) => {
        console.log('badge', badge);
        // there is no point in checking claimability if badge is already achieved
        if (badge.achieved) return badge;
        // skip claimability check if possible
        if (!badge.claimable) {
          // console.log('check', badge);
          try {
            badge.claimable = await badge.check();
            if (badge.claimable) {
              updateExistingBadge(badge, true);
            }
          } catch (error) {
            console.error(error);
          }
        }
        return badge;
      }),
    );
  } catch (error) {
    console.error(error);
  }
  return [];
};

export const getAllBadges = async (): Promise<Badge[]> => {
  try {
    const badgesDocs = await getAllDbBadges();
    console.log('badgesDb', badgesDocs?.rows);
    const unifiedBadges = getUnifiedBadges(badgesList, badgesDocs);
    console.log('unifiedBadges', unifiedBadges);
    const allBadges = await claimableCheck(unifiedBadges);
    console.log('allBadges', allBadges);
    return allBadges;
  } catch (error) {
    console.error(error);
  }
  return [];
};

