import { dbStats } from '.';

export const updateStats = async (name: string, value: number) => {
  let doc: any = null;
  try {
    doc = await dbStats.get(name);
  } catch (err: any) {
    if (err.name !== 'not_found') {
      throw err;
    }
  }
  return await dbStats.put(
    {
      _id: name,
      _rev: doc?._rev,
      value: value,
    },
    { force: true },
  );
};
