import React, { useEffect, useState } from 'react';
import styles from './Badges.module.scss';
import { Header } from '../../common/Header/Header';
import { ButtonsPanel } from '../../common/ButtonsPanel/ButtonsPanel';
import { ButtonBase } from '../../common/ButtonsPanel/ButtonBase/ButtonBase';
import { useNavigate } from 'react-router-dom';
import { SingleBadge } from './SingleBadge/SingleBadge';
import { BadgeDetails } from './BadgeDetails/BadgeDetails';
// import { badgesList } from '../../../shared/db/badges/badgesList';
import { getAllBadges } from '../../../shared/db/badges/getAllBadges';
import { useAsyncFn } from 'react-use';

export const Badges: React.FC = () => {
  const [selectedBadgeId, setSelectedBadgeId] = useState('');
  const navigate = useNavigate();
  const showDetails = (selectedBadgeId: string) => {
    setSelectedBadgeId(selectedBadgeId);
  };
  const hideDetails = () => {
    setSelectedBadgeId('');
  };

  const [{ value: badgesList }, fetch] = useAsyncFn(async () => {
    console.log('start');
    const allBadges = await getAllBadges();
    console.log('allBadges', allBadges);
    return allBadges;
  });

  useEffect(() => {
    fetch();
  }, [fetch]);

  return (
    <div className={styles.root}>
      <div className={styles.header}>
        <Header subtitle="Get unique water badges!" />
      </div>
      {selectedBadgeId && <BadgeDetails selectedBadgeId={selectedBadgeId} />}
      {!selectedBadgeId && (
        <>
          <p>Badges support will be added soon!</p>
          <div className={styles.badgesList}>
            {badgesList &&
              badgesList.map((badge) => (
                <SingleBadge
                  icon={badge.icon}
                  label={badge.label}
                  discovered={badge.available}
                  active={badge.claimable}
                  onClick={showDetails}
                  details={badge.details}
                  key={badge.icon}
                />
              ))}
          </div>
        </>
      )}
      <ButtonsPanel>
        <ButtonBase
          icon="back"
          onClick={() => {
            selectedBadgeId ? hideDetails() : navigate('/');
          }}
          label="return"
        />
      </ButtonsPanel>
    </div>
  );
};
