import { getStats } from './getStats';
import { updateStats } from './updateStats';

export const decrementStats = async (name: string) => {
  try {
    const currentValue = await getStats(name);
    await updateStats(name, currentValue.value - 1);
  } catch (error) {
    console.error(error);
    return 1;
  }
  return 0;
};
